body {
  font-family: "Poppins";
  font-weight: 400;
}

h1, h2, h3, h4, h5 {
  font-family: "Poppins";
  font-weight: 700;
}

::placeholder, button, input, textarea {
  font-family: "Poppins";
  font-weight: 400;
}

html {
  font-size: 100%;
} /* 16px */
h1 {
  font-size: 4.21rem; /* 67.36px */
}

h2 {
  font-size: 3.158rem; /* 50.56px */
}

h3 {
  font-size: 2.369rem; /* 37.92px */
}

h4 {
  font-size: 1.777rem; /* 28.48px */
}

h5 {
  font-size: 1.333rem; /* 21.28px */
}

small {
  font-size: 0.75rem; /* 12px */
}

p {
  text-align: justify;
}

@media (max-width: 1024px) {
  h1 {
    font-size: clamp(1.5rem, 7vw, 3rem);
  }
  h2 {
    font-size: clamp(1.5rem, 7vw, 2.4rem);
  }
}
@media (max-width: 640px) {
  h4 {
    font-size: clamp(1.3rem, 4vw, 2.4rem);
  }
  h5 {
    font-size: clamp(1.2rem, 3vw, 2.4rem);
  }
}
.lang input {
  position: absolute;
}
.lang label {
  position: absolute;
  z-index: 1;
  border-radius: 4px;
  width: 68px;
  height: 20px;
  cursor: pointer;
}

.flag {
  display: flex;
  align-items: center;
  gap: 8px;
}
.flag span {
  border-radius: 4px;
  filter: grayscale(1);
  height: 20px;
  width: 30px;
  cursor: pointer;
}
.flag span:nth-child(1) {
  background: url("/media/icons/french.svg") center/30px no-repeat;
}
.flag span:nth-child(2) {
  background: url("/media/icons/english.svg") center/35px no-repeat;
}
.flag span:hover {
  filter: grayscale(0);
}

body:has(.lang input:checked) .lang .flag span:nth-child(1) {
  filter: grayscale(0);
}

body:has(.lang input:not(:checked)) .lang .flag span:nth-child(2) {
  filter: grayscale(0);
}

body:has(.lang input:checked) [lang-switch=french] {
  display: block;
}
body:has(.lang input:checked) [lang-switch=english] {
  display: none;
}

body:has(.lang input:not(:checked)) [lang-switch=french] {
  display: none;
}
body:has(.lang input:not(:checked)) [lang-switch=english] {
  display: block;
}

[lang-switch] {
  animation: fade-in 0.75s ease-in-out forwards;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#get-to-top {
  position: fixed;
  bottom: 10vh;
  right: 0;
}
#get-to-top button {
  background: var(--white-smoke);
  border: none;
  width: 38px;
  aspect-ratio: 1/1;
  border-radius: 50% 0 0 50%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  position: relative;
  cursor: pointer;
}
#get-to-top button::after {
  content: "";
  position: absolute;
  top: 60%;
  left: 55%;
  transform: translate(-50%, -50%) rotate(45deg);
  border-top: 4px solid var(--rich-black);
  border-left: 4px solid var(--rich-black);
  width: 10px;
  aspect-ratio: 1/1;
}

header {
  z-index: 5;
  padding: 1em;
}

nav {
  display: flex;
  align-items: center;
}

.logo {
  width: 79px;
  height: 63px;
  background: url("/media/icons/logo.webp") center/cover no-repeat;
  filter: drop-shadow(0 0 2px rgba(255, 255, 255, 0.562));
}

.nav-links {
  display: flex;
  gap: 1em;
  margin: 0 1.5em 0 auto;
}
.nav-links a {
  position: relative;
}
.nav-links a::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%) scaleX(0);
  transform-origin: right;
  height: 4px;
  width: 100%;
  background: linear-gradient(to right, var(--canary) 50%, var(--apple-green) 50%);
  transition: transform 0.25s ease-in-out;
}
.nav-links a:hover::after {
  transform: translateX(-50%) scaleX(1);
  transform-origin: left;
}

.menu {
  display: flex;
  gap: 0.6666666667em;
  position: relative;
}
.menu * {
  cursor: pointer;
}
.menu label {
  position: relative;
}
.menu label::after {
  content: "Menu";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: calc(100% + 0.6666666667em);
}
.menu label div {
  width: 20px;
  height: 2px;
  background: var(--rich-black);
}
.menu label div:nth-child(2) {
  margin: 3px 0;
}
.menu input {
  position: absolute;
  opacity: 0;
}

footer {
  padding: 3em 1em 1em;
  background: color-mix(in srgb, var(--picton-blue), transparent 0%);
}
footer .logo {
  width: 108px;
  height: 84px;
  flex-shrink: 0;
  background: url("/media/icons/logo.webp") center/cover no-repeat;
  mix-blend-mode: multiply;
}
footer .principal {
  display: none;
}
@media (max-width: 475px) {
  footer .principal {
    display: flex;
  }
  footer .minimalist {
    display: none;
  }
}

.principal {
  display: flex;
  align-items: center;
  margin: 0 auto 1.75em;
}

.minimalist {
  width: fit-content;
  margin: 0 auto 1.75em;
}
.minimalist .get-in-touch {
  gap: 1.75em;
}

.get-in-touch {
  display: flex;
  flex-wrap: wrap;
  width: fit-content;
  gap: 0.6666666667em;
}
.get-in-touch a {
  font-weight: 500;
  display: flex;
  gap: 0.6666666667em;
  padding-left: 1em;
}
.get-in-touch a span {
  background: var(--white-smoke);
  padding: 2px 8px;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
}
.get-in-touch a span img {
  position: relative;
  top: 4px;
  filter: drop-shadow(0 0 4px rgba(163, 163, 163, 0.4039215686));
}

.links {
  display: grid;
  gap: 2px;
}

.copyright {
  display: flex;
  justify-content: center;
  gap: 4px;
}

/*Desktop First*/
.container {
  max-width: 1536px;
  margin: 0 auto;
}

/*2xl*/
@media (max-width: 1536px) {
  .container {
    max-width: 1280px;
  }
}
@media (min-width: 1536px) {
  .container {
    max-width: 90%;
  }
}
/*xl*/
@media (max-width: 1280px) {
  .container {
    max-width: 1024px;
  }
}
/*lg*/
@media (max-width: 1024px) {
  .container {
    max-width: 768px;
  }
}
/*md*/
@media (max-width: 768px) {
  .container {
    max-width: 640px;
  }
}
/*sm*/
@media (max-width: 640px) {
  .container {
    max-width: 475px;
  }
}
/*xs*/
@media (max-width: 475px) {
  .container {
    width: 100%;
  }
}
#contact {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: color-mix(in srgb, var(--rich-black), transparent 70%);
  backdrop-filter: blur(10px);
  z-index: 2;
  place-content: center;
}
#contact label {
  font-weight: 600;
}
#contact input {
  padding: 0.6666666667em 1em;
}
#contact textarea {
  padding: 1em;
}
#contact input, #contact textarea {
  border-radius: 0.6666666667em;
  border: 2px solid color-mix(in srgb, var(--rich-black), transparent 60%);
  resize: none;
}
#contact input:focus, #contact textarea:focus {
  outline: none;
}
#contact form {
  position: relative;
  display: grid;
  border-radius: 1em;
  padding: 1.75em;
  padding-top: 2em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  position: relative;
  background: white;
  width: 500px;
}
#contact form input {
  margin: 0 0 1em;
}
#contact form::before {
  content: "";
  position: absolute;
  bottom: -0.6666666667em;
  right: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 4px 4px 1.5em 4px;
  background: var(--green-apple);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}
#contact form::after {
  content: "";
  position: absolute;
  top: -0.6666666667em;
  left: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 1.5em 4px 4px 4px;
  background: var(--canary);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}
#contact form .send {
  border: none;
  width: fit-content;
  margin: 1em auto 0;
  padding: 0.6666666667em 2em;
  background: var(--azul);
  font-weight: 600;
  text-transform: uppercase;
  color: white;
  border-radius: 0.6666666667em;
}
#contact form .close {
  position: absolute;
  top: 0.6666666667em;
  right: 0.6666666667em;
  border: none;
  background: none;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  width: 25px;
  aspect-ratio: 1/1;
}
#contact form .close div {
  position: relative;
}
#contact form .close span {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 3px;
  width: 16px;
  background: var(--rich-black);
}
#contact form .close span:nth-child(1) {
  transform: translate(-50%, -50%) rotate(45deg);
}
#contact form .close span:nth-child(2) {
  transform: translate(-50%, -50%) rotate(-45deg);
}
@media (max-width: 640px) {
  #contact form {
    width: 85vw;
  }
}

#hero {
  height: 80vh;
  display: grid;
  align-items: center;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)), url("/media/home/hero.webp") top center/cover no-repeat;
  margin: 1em 1em 3em;
  border-radius: 1em;
  position: relative;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
}
@media (max-width: 1024px) {
  #hero {
    height: clamp(300px, 40vh, 1000px);
  }
}
#hero h1 {
  text-align: center;
  color: var(--white-smoke);
  text-shadow: #000 0px 5px 10px, #000 0px 5px 10px;
  line-height: 1.2;
}
#hero h2 {
  word-spacing: 15px;
  text-align: center;
  font-family: "Arsilon";
  color: var(--canary);
  font-weight: 400;
  text-shadow: #000 0px 5px 10px, #000 0px 5px 10px;
}

.c-t-a {
  display: flex;
  justify-content: center;
  gap: 1em;
  margin: 0.6666666667em 0 0;
}
.c-t-a a {
  background: var(--apple-green);
  color: var(--rich-black);
  padding: 0.6666666667em 1em;
  border-radius: calc(0.6666666667em - 4px);
  font-weight: 600;
  box-shadow: var(--rich-black) 0px 0px 15px, var(--rich-black) 0px 0px 25px;
}
@media (max-width: 768px) {
  .c-t-a a {
    padding: 4px 8px;
  }
}

#swiper {
  margin-top: 9em;
  margin-bottom: 9em;
  display: grid;
  place-content: center;
}
#swiper .swipe {
  display: flex;
  gap: 6em;
  align-items: center;
  position: relative;
  justify-content: space-evenly;
  padding-bottom: 3em;
}
#swiper .illu {
  width: 500px;
  height: 300px;
  border-radius: 1em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  animation: come-in 0.5s ease-in-out forwards;
}
#swiper .text {
  max-width: 600px;
}
#swiper .text :nth-child(1) {
  opacity: 0;
  animation: come-in 0.5s ease-in-out forwards;
}
#swiper .text :nth-child(2) {
  opacity: 0;
  animation: come-in 0.5s ease-in-out 0.05s forwards;
}
#swiper .text :nth-child(3) {
  opacity: 0;
  animation: come-in 0.5s ease-in-out 0.1s forwards;
}
#swiper h5 {
  text-shadow: rgba(0, 0, 0, 0.168627451) 0px 5px 10px, rgba(0, 0, 0, 0.1254901961) 0px 5px 10px;
}
#swiper .pagination {
  position: absolute;
  left: 20%;
  bottom: 0.6666666667em;
  display: flex;
  justify-content: center;
  gap: 1em;
  align-self: flex-end;
}
#swiper .pagination span {
  height: 10px;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background: white;
  cursor: pointer;
  position: relative;
  transition: all 0.25s ease-in-out;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2941176471), 0px 0px rgba(0, 0, 0, 0.308) #000;
}
#swiper .pagination span:hover::after {
  transform: translate(-3px, -50%) scale(1.1);
}
#swiper .pagination span:hover.active::after {
  transform: translate(-3px, -50%) scale(1.1);
}
#swiper .pagination span.active {
  width: 20px;
  border-radius: 5px;
}
#swiper .pagination span.active::after {
  position: absolute;
  content: "";
  top: 50%;
  bottom: 50%;
  transform: translate(-3px, -50%);
  height: 14px;
  width: 24px;
  border-radius: 8px;
  border: 1px solid white;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5333333333);
  transition: transform 0.25s ease-in-out;
}
#swiper .pagination span::after {
  position: absolute;
  content: "";
  top: 50%;
  bottom: 50%;
  transform: translate(-3px, -50%);
  height: 14px;
  border-radius: 50%;
  aspect-ratio: 1/1;
  border: 1px solid white;
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.5333333333);
  transition: transform 0.25s ease-in-out;
}

