  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 6em;
}
@media (max-width: 475px) {
  #founder {
    padding: 0 1.5em;
  }
}
@media (max-width: 1024px) {
  #founder {
    flex-direction: column;
  }
  #founder .image {
    position: initial;
    order: 1;
    clip-path: initial;
    width: 100%;
    height: 250px;
    margin: 0 0 1.75em;
    border-radius: 1em;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
    background: url("/media/home/founder.webp") top center/cover no-repeat;
  }
  #founder .image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
  }
  #founder .text {
    order: 2;
  }
}
#founder .image {
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
  background: url("/media/home/founder.webp") 0% 50%/cover no-repeat;
  height: 55vh;
  width: 500px;
}
#founder h4 {
  line-height: 1.2;
}
#founder h5 {
  margin: 0 0 0.6666666667em;
}
#founder .verse {
  font-weight: 500;
}
#founder p:nth-child(5) {
  font-weight: 600;
}
#founder p:not(:nth-child(5)) {
  margin: 0 0 0.6666666667em;
}
#founder a {
  background: var(--picton-blue);
  padding: 0.6666666667em 1em;
}
#founder .text {
  max-width: 65ch;
}

#map {
  display: flex;
  gap: 3em;
  justify-content: center;
  margin-bottom: 6em;
}
#map .illu {
  text-align: center;
}
#map .mada {
  width: 275px;
}
#map .mada img {
  width: 100%;
  height: 100%;
}

#title {
  margin-top: 1em;
  height: 200px;
  text-align: center;
  display: grid;
  place-content: center;
  background: url("/media/icons/Sprinkle.svg");
}
